import React, { Component } from 'react';
import Context from '../context/context.js'

import {Page, Container, Row} from '../components/Layout.js'
import Input from '../components/Input.js'
import Label from '../components/Label.js'
import Button from '../components/Button.js'
import PlayerRow from '../components/PlayerRow.js'

import {copy} from '../utils/utils.js'

class Lobby extends Component {
  constructor() {
    super();
    this.state = {
      players: [
        {id: '', ready: false}
      ],
      code: ''
    }
  }

  componentDidMount() {
    this.context.socket.emit('checkId', this.props.match.params.gameId)

    this.context.socket.on('idChecked', ({checked,code})=> {
      if(!checked) {
        this.props.history.push('/')
      }
      else {
        console.log(code);
        this.setState({code})
      }
    })

    this.context.socket.on('join', (player)=> {
      let players = this.state.players
      players.push(player)
      this.setState({players})
    })

    this.context.socket.on('ready', (id) => {
      let {players} = this.state

    })
  }

  render() {
    return(
      <Page>
        <Container justify="center" align="center">
          {this.state.players.map((player,i)=> {
            return <Row key={`player-${i}`} width="50%"><PlayerRow name={`Player ${i+1}`}  /></Row>
          })}
        </Container>
        <Container justify="space-evenly" align="center">
          <Row width="50%"><Button onClick={this.ready}>Ready</Button></Row>
          <Row width="50%" align="center">
            <Label color="#eeeeee">Invite Code:</Label>
            <Button justify="flex-start" icon="content_copy" onClick={copy}>{this.state.code}</Button>
          </Row>
        </Container>
      </Page>
    )
  }
}
Lobby.contextType = Context

export default Lobby
