import React from 'react';
import styled from 'styled-components'

function PlayerRow(props) {
  return (
    <Row><Name>{props.name}</Name><Name><i className="material-icons">check</i></Name></Row>
  )
}

export default PlayerRow

const Row = styled.div`
  width: 100%;
  height: 3em;
  margin-bottom: 4em;
  border: 5px solid #eeeeee;
  border-radius: 2em;
  background-color: #263238;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const Name= styled.p`
  padding: 1em;
  font-size: 18px;
  color: #eeeeee;
`
