import React, { Component } from 'react';
import Context from '../context/context.js'
import styled from 'styled-components'

class Canvas extends Component {
  constructor() {
    super();
    this.state = {
      width: 0,
      height: 0,
      x: 100,
      y: 400,
      radius: 50,
      spawnTime:0
    }
  }

  componentDidMount() {
    this.context.socket.emit('getDimensions', undefined)
    this.context.socket.on('dimensions', ({width,height}) => {
      this.setState({width,height})
    })
    let ctx = this.canvas.getContext('2d')
    ctx.fillStyle = '#ffffff'
    this.context.socket.on('draw', ({x,y})=> {
      this.setState(x,y)
      setTimeout(()=> {
        this.drawCircle(ctx)
      }, 3000)
    })
    this.canvas.addEventListener('click',(e)=> {
      this.checkChords(e)
    })
  }

  drawCircle(ctx,x,y) {
    ctx.beginPath();
    ctx.arc(this.state.x, this.state.y, this.state.radius, 0, 2 * Math.PI);
    ctx.fill();
    this.setState({spawnTime: Date.now()})
  }

  checkChords(e)  {
    let a = e.offsetX - this.state.x
    let b = e.offsetY - this.state.y
    let c = Math.floor(Math.sqrt((a * a) + (b * b)))
    if(c < this.state.radius) {
      let time = Date.now() - this.state.spawnTime
      this.context.socket.emit('clicked', time)
    }
  }

  render() {
    return (
      <CanvasContainer width={this.state.width} height={this.state.height}>
        <canvas ref={(canvas) => this.canvas = canvas} width={this.state.width} height={this.state.height}></canvas>
      </CanvasContainer>
    )
  }

}

export default Canvas
Canvas.contextType = Context

const CanvasContainer = styled.div`
  margin-top: 5em;
  width: ${props => props.width};
  height: ${props => props.height};
  padding: 2em;
  border-radius: 2em;
  background-color: #263238;
`
